import {ADD_TODO, TOGGLE_TODO, REMOVE_TODO} from './ActionTypes.js';

let NextTodoId = 0;

export const addTodo = (text) => ({
    type: ADD_TODO,
    completed: false,
    id: NextTodoId ++,
    text: text,
});

export const toggleTodo = (id) => ({
    type: TOGGLE_TODO,
    id: id,
})

export const removeTodo = (id) => ({
    type: REMOVE_TODO,
    id: id,
})


